import React, { useEffect, useState } from "react";
import "./Order.scss";
import { useForm } from "react-hook-form";
import axios from "axios";
import { toast } from "react-toastify";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { CustomContext } from "../../Context/Context";

//* SVG /-----------------------------------------------
import { ReactComponent as CardIcon } from "../../../assets/icons/icon-card.svg";
import { ReactComponent as WalletIcon } from "../../../assets/icons/icon-wallet.svg";
import { ReactComponent as IconPlus } from "../../../assets/icons/icon-plus.svg";
import { ReactComponent as IconMinus } from "../../../assets/icons/icon-minus.svg";
//* SVG /-----------------------------------------------


import { InputRadio } from "./InputRadio";
import { Input } from "./Input";
import { OrderButton } from "./OrderButton";
import { Checkbox } from "./Checkbox";
import { IconButton } from "../../Buttons/IconButton";
import { CartTotalContainer } from "../Cart/CartTotalContainer";
import { Modal } from "../../Modal/Modal";
import { ModalContent } from "./ModalContent";

export const Order = () => {
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);

  const {
    register,
    handleSubmit,
    setValue,
    formState: { errors },
    reset,
  } = useForm();

  const [deliveryOption, setDeliveryOption] = useState("'");
  const [payOption, setPayOption] = useState("");
  const [stickAmount, setStickAmount] = useState(1);

  useEffect(() => {
    setValue("delivery", deliveryOption);
  }, [setValue, deliveryOption]);
  useEffect(() => {
    setValue("pay", payOption);
  }, [setValue, payOption]);
  useEffect(() => {
    setValue("stickAmount", stickAmount);
  }, [setValue, stickAmount]);

  const chooseOption = (option) => {
    switch (option) {
      case "'":
      case "":
        setDeliveryOption(option);
        break;
      case "":
      case "":
        setPayOption(option);
        break;

      default:
        break;
    }
  };

  const { cart, totalPrice, orderDiscount, deliveryCost, emptyart } =
    useContext(CustomContext);

  const TOKEN = "6275533372:AAE8QMB0Cznj68Kw4xmhctaa2aVOze3D95A";
  const CHAT_ID = "-1001850985728";
  const URI_API = `https://api.telegram.org/bot${TOKEN}/sendMessage`;

  const onSubmit = (data) => {

    if (!cart.length) {
      toast.warn("   .");
      return;
    }

    let message = `<b></b>\n \n`;
    message = `<b> </b>\n \n`;
    message += `<b>': </b>${data.firstName}\n`;
    message += `<b>: </b>${data.lastName}\n`;
    message += `<b> : </b>${data.delivery}\n`;
    message += `<b>: </b>${data.street}\n`;
    message += `<b>: </b>${data.house}\n`;
    message += `<b>: </b>${data.apartment}\n`;
    message += `<b>': </b>${data.entrance}\n`;
    message += `<b>: </b>${data.floor}\n`;
    message += `<b> ': </b>${data.code}\n`;
    message += `<b> : </b>${data.pay}\n`;
    message += `<b>  : </b>${data.change}\n`;
    message += `<b>: </b>${data.phone}\n`;
    message += `<b>  : </b>${data.comment}\n`;
    message += `<b> +  : </b>${data.stickAmount}\n \n`;

    message += `<b>  </b>\n \n`;
    cart.forEach((product, idx) => {
      message += `<b>  ${idx + 1}: </b>${product.name}
  ${product.category === "pizza" ? ": " + product.size : ""} 
  : ${product.count}\n\n`;
    });
    message += `<b>  : </b>${totalPrice} .\n`;
    message += `<b>: </b>${orderDiscount} .\n`;
    message += `<b>   : </b>${
      totalPrice - orderDiscount
    } .\n`;
    message += `<b> : </b>${deliveryCost}\n`; console.log(message);

    axios.post(URI_API, {
      chat_id: CHAT_ID,
      parse_mode: "html",
      text: message,
    });
    reset();
    openModal();
    emptyart();
  };

  const openModal = () => {
    document.body.style.overflow = "hidden";
    setShowModal(true);
  };
  const closeModal = () => {
    document.body.style.overflow = "";
    setShowModal(false);
    navigate("/");
  };

  return (
    <>
      <section className="order">
        <h2 className="order__title"> </h2>
        <form className="order-form" onSubmit={handleSubmit(onSubmit)}>
          <div className="order-form__block">
            <Input
              register={register}
              name={"firstName"}
              placeholder={"'"}
              errors={errors}
            />
            <Input
              register={register}
              name={"lastName"}
              placeholder={""}
              errors={errors}
            />
          </div>
          <div className="order-form__block">
            <InputRadio
              option={"delivery"}
              value={deliveryOption}
              register={register}
            />
            <InputRadio
              option={"delivery"}
              value={deliveryOption}
              register={register}
            />
            <OrderButton
              option={deliveryOption}
              chooseOption={chooseOption}
              name={"'"}
            />
            <OrderButton
              option={deliveryOption}
              chooseOption={chooseOption}
              name={""}
            />
          </div>
          <div className="order-form__block">
            <Input
              register={register}
              name={"street"}
              placeholder={""}
              errors={errors}
            />
          </div>
          <div className="order-form__block">
            <Input
              register={register}
              name={"house"}
              placeholder={""}
              errors={errors}
            />
          </div>

          <div className="order-form__block">
            <Input register={register} name={"apartment"} placeholder={""} />
            <Input
              register={register}
              name={"entrance"}
              placeholder={"'"}
            />
          </div>
          <div className="order-form__block">
            <Input register={register} name={"floor"} placeholder={""} />
            <Input
              register={register}
              name={"code"}
              placeholder={" '"}
            />
          </div>

          <div className="order-form__block">
            <InputRadio option={"pay"} value={payOption} register={register} />
            <InputRadio option={"pay"} value={payOption} register={register} />
            <OrderButton
              option={payOption}
              chooseOption={chooseOption}
              name={""}
            >
              <WalletIcon className="order-form__icon" />
            </OrderButton>
            <OrderButton
              option={payOption}
              chooseOption={chooseOption}
              name={""}
            >
              <CardIcon className="order-form__icon" />
            </OrderButton>
          </div>

          <div className="order-form__checkbox-container">
            <label className="order-form__checkbox-field">
              <Checkbox register={register} />
              <span>  </span>
              <input {...register("change")} type="text" placeholder="" />
            </label>
          </div>

          <div className="order-form__block">
            <Input
              register={register}
              name={"phone"}
              placeholder={""}
              errors={errors}
            />
          </div>
          <div className="order-form__block">
            <Input
              register={register}
              name={"comment"}
              placeholder={"  "}
            />
          </div>

          <div className="order-form__block">
            <span>  +  </span>
            <div className="order-form__quantity-button-container">
              <IconButton
                aria-label=" "
                onClick={() => {
                  setStickAmount(stickAmount + 1);
                }}
              >
                <IconPlus width={"20px"} height={"20px"} />
              </IconButton>
              <span className="order-form__quantity">{stickAmount}</span>
              <IconButton
                style={{ backgroundColor: "transparent" }}
                aria-label=" "
                onClick={() => {
                  setStickAmount(
                    stickAmount === 0 ? stickAmount : stickAmount - 1
                  );
                }}
              >
                <IconMinus width={"20px"} height={"20px"} />
              </IconButton>
            </div>
          </div>
          {cart.length ?  <div className="order__total">
            <CartTotalContainer />
          </div> : ""}
          <button type="submit" className="order-form__submit-button">
             
          </button>
          <p className="order-form__text">
                ,   
                  {" "}
            <a href="/#" className="order-form__link">
               
            </a>
          </p>
        </form>
        {showModal && (
          <Modal closeModal={closeModal}>
            <ModalContent />
          </Modal>
        )}
      </section>
    </>
  );
};
